import React from "react";
import styles from "./about-css-modules.module.css";
console.log(styles);

import Container from "../components/container";

const User = props =>
  <div className={styles.user}>
    <img src={props.avatar} className={styles.avatar} alt="" />
    <div className={styles.description}>
      <h2 className={styles.username}>
        {props.username}
      </h2>
      <p className={styles.excerpt}>
        {props.excerpt}
      </p>
    </div>
  </div>

export default () =>
  <Container>
    <h1>About Me.</h1>
    <User
      username="Bennett Yong"
      avatar="../profile.png"
      excerpt="I'm a Student at the University of Warwick, studying Computer Systems Engineering. This blog is meant to track my progress and learning of React.js as well as using Gatsby.js to create sites."
    />


  </Container>


